<template>
  <div class="container">
    <header>
      <h3>İzinliler</h3>
    </header>

    <div class="table-wrapper">
      <table class="table-users">
        <tr>
          <th style="width: 30%">Talebe İsmi</th>
          <th style="width: 10%">Grup</th>
          <th style="width: 20%">İzin Nevisi</th>
          <th style="width: 10%">Çıkış Saati</th>
          <th style="width: 10%">Geliş Saati</th>
          <th style="width: 20%">İşlem</th>
        </tr>

        <tr v-for="izinli in IzinlilerDizi" :key="izinli.key">
          <td>{{ izinli.izinli_isim }}</td>
          <td>{{ izinli.izinli_grup }}</td>
          <td>{{ izinli.izinli_nevi }}</td>
          <td>{{ izinli.izinli_cikis_saati }}</td>
          <td>{{ izinli.izinli_gelis_saati }}</td>
          <td>
            <router-link
              :to="{ name: 'izinliedit', params: { id: izinli.key } }"
              class="btn btn-edit"
            >
              <i class="material-icons">create</i>
            </router-link>
            <button
              @click.prevent="deleteIzinli(izinli.key)"
              class="btn btn-delete"
            >
              <i class="material-icons">remove_circle_outline</i>
            </button>
          </td>
        </tr>
      </table>

      

      <router-link to="/izinliadd">
         <button class="btn btn-add">İzinli Ekle</button>
      </router-link>

      
      
    </div>
  </div>

</template>

<script>
import { db } from "../firebaseDb";

export default {
  data() {
    return {
      IzinlilerDizi: [],
    };
  },
  created() {

db.collection("izinliler").orderBy("izinli_grup", "asc").onSnapshot((snapshotChange) => {
      this.IzinlilerDizi = [];
      snapshotChange.forEach((doc) => {
        this.IzinlilerDizi.push({
          key: doc.id,
          izinli_isim: doc.data().izinli_isim,
          izinli_grup: doc.data().izinli_grup,
          izinli_nevi: doc.data().izinli_nevi,
          izinli_cikis_saati: doc.data().izinli_cikis_saati,
          izinli_gelis_saati: doc.data().izinli_gelis_saati,
        });
      });
    });   
  },
  methods: {
    deleteIzinli(id) {
      if (window.confirm("Silmek İstiyor musunuz?")) {
        db.collection("izinliler")
          .doc(id)
          .delete()
          .then(() => {
            console.log("Document deleted!");
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
       


  },
};
</script>

