import Vue from 'vue'
import VueRouter from 'vue-router'

import IzinliList from '../components/IzinliList.vue'

Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    name: 'izinlilist',
    component: IzinliList
  },

  {
    path: '/izinliadd',
    name: 'izinliadd',
    component: () => import('../components/IzinliAdd')
  },

  {
    path: '/izinliedit/:id',
    name: 'izinliedit',
    component: () => import('../components/IzinliEdit')
  },

  {
    path: '/personellist',
    name: 'personellist',
    component: () => import('../components/PersonelList')
  },

  {
    path: '/personeladd',
    name: 'personeladd',
    component: () => import('../components/PersonelAdd')
  },

  {
    path: '/personeledit/:id',
    name: 'personeledit',
    component: () => import('../components/PersonelEdit')
  },

  {
    path: '/yemekcilist',
    name: 'yemekcilist',
    component: () => import('../components/YemekciList')
  },
  
  {
    path: '/yemekciadd',
    name: 'yemekciadd',
    component: () => import('../components/YemekciAdd')
  },

  {
    path: '/yemekciedit/:id',
    name: 'yemekciedit',
    component: () => import('../components/YemekciEdit')
  },

  {
    path: '/araclist',
    name: 'araclist',
    component: () => import('../components/AracList')
  },

  {
    path: '/aracadd',
    name: 'aracadd',
    component: () => import('../components/AracAdd')
  },
  {
    path: '/aracedit/:id',
    name: 'aracedit',
    component: () => import('../components/AracEdit')
  },

  {
    path: '/notlist',
    name: 'notlist',
    component: () => import('../components/NotList')
  },
  
  {
    path: '/notadd',
    name: 'notadd',
    component: () => import('../components/NotAdd')
  },
  
  {
    path: '/notedit/:id',
    name: 'notedit',
    component: () => import('../components/NotEdit')
  },
  
  {
    path: '*',
    component: IzinliList
  },
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
